import React from 'react'

const Toast = ({ MsgAlert }) => {
	return (
		<div>
			<div 
			className={MsgAlert.tipo === "danger"?"alert alert-danger text-center":MsgAlert.tipo === "success"?"alert alert-success text-center":"alert text-center"} role="alert">
				{MsgAlert.msg}
			</div>
		</div>
	)
}

export default Toast