import React, { useState, useEffect } from 'react'
import moment from 'moment'



const Table = () => {

	const [items, setItems] = useState([])

	const saveLocal = async (item) => {
		await localStorage.setItem('agenda-todos', JSON.stringify(item))
	}

	const getItmes = () => {
		if(localStorage.getItem('events')){
			let localEvents = JSON.parse(localStorage.getItem('events'))
			setItems(localEvents)
		}else{
			alert('not found')
		}
	}

	const deleteEvent = (id) => {
		let newItems = items.filter(item => item !== id)
		setItems(newItems)
		saveLocal(newItems)
	}

	useEffect(() => {
		getItmes()
	}, [])

	return (
		<div>
			<table className="table">
				<thead>
					<tr>
						<th scope="col">Name</th>
						<th scope="col">Start</th>
						<th scope="col">End</th>
						<th scope="col">Clase</th>
						<th scope="col">Actions</th>
					</tr>
				</thead>
				<tbody>
					{
						items.map(item => 
							<tr key={item._id} >
								<th scope="row">{item.name}</th>
								<td>{moment(item.startDateTime).format("HH:mm")}</td>
								<td>{moment(item.endDateTime).format("HH:mm")}</td>
								<td>{item.classes}</td>
								<td>
									<button className="btn btn-warning" onClick={() => deleteEvent(item)} >delete</button>
								</td>
							</tr>
						)
					}
				</tbody>
			</table>
		</div>
	)
}

export default Table