import React, { useState } from 'react'
import { Link } from 'react-router-dom'
//componentes
import Table from './Table'
import Todo from './Todo'
import List from './List'

export default function Stats() {

	const [page, setPage] = useState("todo")

	return (

		<div>
			<div className="d-flex justify-content-between mb-2">
				<Link to="/agenda-app/" className="btn btn-primary my-2 ml-2 btn-sm align-self-center">Back</Link>
				<ul className="nav nav-pills d-flex justify-self-content-center my-1">
					<li className="nav-item">
						<a className={page === "todo"?"nav-link active":"nav-link"} onClick={() => setPage("todo")}>Todo</a>
					</li>
					<li className="nav-item">
						<a className={page === "table"?"nav-link active":"nav-link"} onClick={() => setPage("table")}>Tables</a>
					</li>
					<li className="nav-item">
						<a className={page === "list"?"nav-link active":"nav-link"} onClick={() => setPage("list")}>List</a>
					</li>
				</ul>
				<Link to="/agenda-app/settings" className="btn btn-primary my-1 mr-2 btn-sm align-self-center">Settings</Link>
			</div>
			{
				page === "todo"?
				<Todo/>
				:page === "table"?
				<Table/>
				:page === "list"?
				<List/>:<></>
			}
		</div>
	)
}
