import React, { Component } from 'react';
import moment from 'moment';
import { ReactAgenda , ReactAgendaCtrl, Modal } from 'react-agenda';
import "react-agenda/build/styles.css"
import "react-datetime/css/react-datetime.css"
import { items, colors } from '../Constants/consts.js'
import { Link } from 'react-router-dom'


var now = new Date(); 

require('moment/locale/es.js');

export default class Agenda extends Component {
  constructor(props){
    super(props);
    this.state = {
      items:[],
      selected:[],
      cellHeight:(60 / 4),
      showModal:false,
      locale:"es",
      rowsPerHour:2,
      numberOfDays:3,
      startDate: new Date(),
      localItems: JSON.parse(localStorage.getItem('events'))
    }
    this.handleRangeSelection = this.handleRangeSelection.bind(this)
    this.handleItemEdit = this.handleItemEdit.bind(this)
    this.zoomIn = this.zoomIn.bind(this)
    this.zoomOut = this.zoomOut.bind(this)
    this._openModal = this._openModal.bind(this)
    this._closeModal = this._closeModal.bind(this)
    this.addNewEvent = this.addNewEvent.bind(this)
    this.removeEvent = this.removeEvent.bind(this)
    this.editEvent = this.editEvent.bind(this)
    this.changeView = this.changeView.bind(this)
    this.handleCellSelection = this.handleCellSelection.bind(this)
  }

  componentDidMount(){
    let it = {}
    this.setState({localItems: JSON.parse(localStorage.getItem('events'))})
    let ev = this.state.localItems.map(event => {
      let s = Date.parse(event.startDateTime)
      let e = Date.parse(event.endDateTime)
      it = {
        _id: event._id,
        name: event.name,
        startDateTime: new Date(s),
        endDateTime: new Date(e),
        classes: event.classes
      }
      return it
    })
    this.setState({items:ev || items})
  }

  componentWillReceiveProps(next , last){
    if(next.items){
      this.setState({items:next.items})
      console.log('will update', next)
    }
  }

  handleItemEdit(item, openModal) {
    if(item && openModal === true){
      this.setState({selected:[item] })
      return this._openModal();
    }
  }

  handleCellSelection(item, openModal) {
    if(this.state.selected && this.state.selected[0] === item){
      return  this._openModal();
    }
       this.setState({selected:[item] })
  }

  zoomIn(){
    var num = this.state.cellHeight + 15
    var row = this.state.rowsPerHour * 2
    this.setState({cellHeight:num})
    this.setState({rowsPerHour:row})
  }
  zoomOut(){
    var num = this.state.cellHeight - 15
    var row = this.state.rowsPerHour / 2
    this.setState({cellHeight:num})
    this.setState({rowsPerHour:row})
  }


  handleDateRangeChange (startDate, endDate) {
      this.setState({startDate:startDate })
  }

  handleRangeSelection (selected) {
    this.setState({selected:selected , showCtrl:true})
    this._openModal();
  }

  _openModal(){
    this.setState({showModal:true})
  }
  _closeModal(e){
    if(e){
      e.stopPropagation();
      e.preventDefault();
    }
      this.setState({showModal:false})
  }

  handleItemChange(items , item){
    this.setState({items:items})
    this.saveLocal(items)
  }

  handleItemSize(items , item){
    this.setState({items:items})
    this.saveLocal(items)
  }

  removeEvent(items , item){
    this.setState({ items:items});
    this.saveLocal(items)

  }

  addNewEvent (items , newItems){
    this.setState({showModal:false ,selected:[] , items:items});
    this._closeModal();
    this.saveLocal(items)
  }

  editEvent (items , item){
    this.setState({showModal:false ,selected:[] , items:items});
    this._closeModal();
    this.saveLocal(items)

  }

  changeView (days , event ){
    this.setState({numberOfDays:days})
  }

  nothing(){

  }

  saveLocal(items){
    localStorage.setItem('events', JSON.stringify(items))
  }

  render() {
    return (
      <div className="content-expanded ">
        <div className="control-buttons">
          <button  className="button-control mas" onClick={this.state.rowsPerHour < 8 ? this.zoomIn : this.nothing}> <i className="zoom-plus-icon">+</i> </button>
          <button  className="button-control menos" onClick={this.state.rowsPerHour > 1 ? this.zoomOut : this.nothing}> <i className="zoom-minus-icon">-</i> </button>
          <button  className="btn btn-primary btn-sm mid mx-2" onClick={this._openModal}> <i className="schedule-icon">N</i> </button>
          <button  className="button-control mas" onClick={this.changeView.bind(null , 7)}> {moment.duration(7, "days").humanize()}  </button>
          <button  className="button-control mid" onClick={this.changeView.bind(null , 3)}> {moment.duration(3, "days").humanize()}  </button>
          <button  className="button-control menos" onClick={this.changeView.bind(null , 1)}> {moment.duration(1, "day").humanize()} </button>
          <Link  className="btn btn-primary btn-sm more mid" to="/agenda-app/stats">More</Link>
        </div>

        <ReactAgenda
          minDate={new Date(now.getFullYear(), now.getMonth()-1)}
          maxDate={new Date(now.getFullYear(), now.getMonth()+3)}
          startDate={this.state.startDate}
          startAtTime={0}
          endAtTime={23}
          cellHeight={this.state.cellHeight}
          locale="es"
          items={this.state.items}
          numberOfDays={this.state.numberOfDays}
          headFormat={"ddd DD MMM"}
          rowsPerHour={this.state.rowsPerHour}
          itemColors={colors}
          helper={true}
          //itemComponent={AgendaItem}
          view="calendar"
          autoScale={false}
          fixedHeader={true}
          onRangeSelection={this.handleRangeSelection.bind(this)}
          onChangeEvent={this.handleItemChange.bind(this)}
          onChangeDuration={this.handleItemSize.bind(this)}
          onItemEdit={this.handleItemEdit.bind(this)}
          onCellSelect={this.handleCellSelection.bind(this)}
          onItemRemove={this.removeEvent.bind(this)}
          onDateRangeChange={this.handleDateRangeChange.bind(this)} 
        />
        {
          this.state.showModal? 
          <Modal clickOutside={this._closeModal} >
            <div className="modal-content">
              <ReactAgendaCtrl 
                items={this.state.items} 
                itemColors={colors}
                selectedCells={this.state.selected} 
                Addnew={this.addNewEvent} 
                edit={this.editEvent}  
              />
            </div>
          </Modal>
          :''
        }
      </div>
    );
  }
}
