import React, { useState, useEffect } from 'react'
import Toast from './Toast'

const getTodos = async () => {
	if(await !JSON.parse(localStorage.getItem('agenda-todos'))){
		await localStorage.setItem('agenda-todos', JSON.stringify([]))
	}
	const localTodos = await JSON.parse(localStorage.getItem('agenda-todos'))
	return localTodos 
}

const Todo = () => {

	const [inputTask, setInputTask] = useState("")
	const [inputProjet, setInputProjetc] = useState("")
	const [todos, setTodos] = useState([])
	const [onProject, setOnProject] = useState([])
	const [onTask, setOnTask] = useState([])
	const [isloading, setIsLoading] = useState(true)
	const [showError, setShowError] = useState(false)
	const [msgAlert, setMsgAlert] = useState({tipo:"",msg:""})
	const [onEditProject, setOnEditProject] = useState(false)
	const [onEditTask, setOnEditTask] = useState(false)

	const addTask = (e) => {
		e.preventDefault()
		if(onEditTask){
			let newtask = {...onTask}
			newtask.title = inputTask
			newtask.date = new Date()
			let newOnProject = [...onProject]
			let i = onProject[1].indexOf(onTask)
			newOnProject[1][i] = newtask
			let j = todos.indexOf(onProject)
			let newTodos = [...todos]
			newTodos[j] = newOnProject
			setTodos(newTodos)
			setOnProject(newTodos[j])
			setOnTask(newOnProject[i])
			saveLocal(newTodos)
			setOnEditTask(false)
			setInputTask("")
			return msgError({tipo:"success",msg:"Se modifico la Tarea Exitosamente"})
		}
		if(onProject.length > 0 && !onEditTask){
			if(inputTask === ""){
				msgError({tipo:"danger",msg:"Debe ingresar un texto."})
			}else {
				let i = todos.indexOf(onProject)
				const newtask = {
					date: new Date(),
					title: inputTask,
					completed: false,
				}
				let newTasks = [...onProject[1]]
				newTasks.unshift(newtask)
				let newOnProject = [onProject[0], newTasks]
				let newtodos = [...todos]
				newtodos[todos.indexOf(onProject)] = newOnProject
				setOnProject(newtodos[i])
				setTodos(newtodos)
				saveLocal(newtodos)
				setInputTask("")
				msgError({tipo:"success",msg:"Tarea creada con exito."})
			}	
		}else {
			msgError({tipo:"danger",msg:"Debe selecconar un Proyecto."})
		}
	}

	const saveLocal = async (item) => {
		await localStorage.setItem('agenda-todos', JSON.stringify(item))
	}

	const addProject = (e) => {
		e.preventDefault()
		if(onEditProject){
			let newProject = [...onProject]
			newProject[0].title = inputProjet
			newProject[0].date = new Date()
			let i = todos.indexOf(onProject)
			let newTodos = [...todos]
			newTodos[i] = newProject
			setInputProjetc("")
			setTodos(newTodos)
			setOnProject(newProject)
			setOnEditProject(false)
			saveLocal(newTodos)
			return msgError({tipo:"success",msg:"Se Guardó el Proyecto modificado Exitosamente"})
		}
		if(inputProjet === "" && !onEditProject){
			msgError({tipo:"danger",msg:"Debe ingresar un titulo."})			
		}else {
			let newProject = [
				{
					title: inputProjet,
					date: new Date()
				},
				[]
			]
			const newTodos = [...todos]
			newTodos.unshift(newProject)
			setTodos(newTodos)
			setOnProject(newProject)
			saveLocal(newTodos)
			setInputProjetc("")
			msgError({tipo:"success",msg:"Proyecto creado con exito."})
		}
	}

	const changeProject = (id) => {
		!onEditProject?
		setOnProject(todos[todos.indexOf(id)])
		:msgError({tipo:"danger", msg:"No puede Cambiar de Proyecto mientras se esté Editando"})
	}

	const changeCompleteTask = (id) => {
		let newTask = {...id}
		newTask.completed = !newTask.completed
		let i = onProject[1].indexOf(id)
		let newOnProject = [...onProject]
		newOnProject[1][i] = newTask
		let j = todos.indexOf(onProject)
		let newTodos = [...todos] 
		newTodos[j] = newOnProject
		setTodos(newTodos)
		setOnProject(newOnProject)
		saveLocal(newTodos)
	}

	const deleteProject = (id) => {		
		let newtodos = todos.filter(todo => todo !== onProject)
		setTodos(newtodos)
		setOnProject([])
		saveLocal(newtodos)	
		msgError({tipo:"success",msg:"Se elimino el Proyecto Exitosamente."})
	}

	const editProject = (id) => {
		setInputProjetc(onProject[0].title)
		setOnEditProject(true)
	}

	const deleteTask = (id) => {
		let i = todos.indexOf(onProject)
		let newTasks = onProject[1].filter(task => task !== id)
		let newOnProject = [...onProject]
		newOnProject[1] = newTasks
		let newTodos = [...todos]
		newTodos[i] = newOnProject
		setTodos(newTodos)
		saveLocal(newTodos)
		setOnProject(newTodos[i])
		msgError({tipo:"success",msg:"Se elimino la Tarea Exitosamente."})
	}

	const editTask = (id) => {
		setInputTask(id.title)
		setOnEditTask(true)
		setOnTask(id)
	}

	const fetchTodos = async () => {
		await getTodos()
			.then((newtodos) => {
				setTodos(newtodos)	
		})
	}

	const msgError = (msg) => {
		setMsgAlert(msg)
		setShowError(true)
		setTimeout(() => {
			setShowError(false)
		}, 2000)
	}

	useEffect(() => {
		fetchTodos()
		setIsLoading(false)
	}, [])

	useEffect(() => {
		if(todos.length > 0 && onProject.length === 0){
			setOnProject(todos[0])
			//setTasks(onProject[1])
		}else if(todos.length > 0 && onProject.length > 0){
			//setTasks(onProject[1])
		}
	}, [todos])

	return (
		<div className="container">
			<div className="row">
				{
					isloading?
					<h3>LOADING</h3>
					:<div className='col-md-3 col-sm-3'>
						<div className="">
							<h3 className="ml-3">Projects</h3>
							<form className="d-flex" >
								<input 
									className="form-controll " 
									type="text" 
									placeholder="Add a new Project" 
									value={inputProjet}
									onChange={(e) => setInputProjetc(e.target.value)}
								/>
								<button 
									className={!onEditProject?"btnr btn-primary btn-sm":"btnr btn-success btn-sm"}
									onClick={(e) => addProject(e)}
								>{!onEditProject?"+":"Edit"}</button>
							</form>
							{
								onProject.length > 0?
								<div className="m-1 d-flex justify-content-around" >
									<button className="btn btn-primary btn-sm" onClick={() => editProject()} >Edit</button>
									<button className="btn btn-primary btn-sm" onClick={() => deleteProject(onProject)} >Del</button>
								</div>
								:<></>
							}
						</div>
						<hr/>
						<ul>
						{
							todos.length > 0?
							todos.map(project => (
								<li 
									key={project[0].date}
									onClick={() => changeProject(project)}
									className={project === onProject?"isSelected":""}
								>
									{project[0].title}
								</li>
							))
							:<h5>No Projects</h5>		
						}
						</ul>
					</div>
				}
				
				<div className="col-md-9 col-sm-9">
					<div className="d-flex justify-content-between">
						<h3 className="ml-3">Tasks</h3>
						<form className="d-flex">
							<input 
								className="form-controll" 
								type="text" 
								placeholder="Add a new Task" 
								value={inputTask}
								onChange={(e) => setInputTask(e.target.value)}
							/>
							<button 
								className={!onEditTask?"btnr btn-primary btn-sm":"btnr btn-success btn-sm"} 
								type="submit"
								onClick={(e) => addTask(e)}
							>
								{!onEditTask?"+":"Edit"}
							</button>
						</form>
					</div>
					<hr/>
					<div className="card m-2 border-0">
					{
						onProject.length > 0?
							onProject[1].length > 0?
							onProject[1].map(task => (
								<div key={task.date}>
								<div className="card-content d-flex justify-content-between align-items-center">
									<h6 className="m-1">{task.title}</h6>
									<div>									
										<button 
											className={
												task.completed?"btn btn-success btn-sm":"btn btn-outline-success btn-sm"
											} 
											onClick={() => {
												changeCompleteTask(task)
											}} 
											>
												{task.completed?"Terminado":"Terminar"}
											</button>
										<button className="btn btn-primary btn-sm mx-1" onClick={() => editTask(task)} >Edit</button>
										<button className="btn btn-primary btn-sm" onClick={() => deleteTask(task)} >Dele</button>
									</div>
								</div>	
								<hr/>
								</div>
							))
							:<h4>No Task</h4>
						:<h4>No Project Selected</h4>
					}	
					</div>	
				</div>	
				{
				showError?
				<div className="container alerta">
					<Toast MsgAlert={msgAlert} />
				</div>
				:<></>
				}
			</div>
		</div>
	)
}

export default Todo