import React from 'react';
import Agenda from './components/ReactAgenda';
import Stats from './components/Stats';
import {Settings} from './components/Settings';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
//import Test from './components/Test';

const local = () => {
	let x = []
	if(!localStorage.getItem('events')){
		localStorage.setItem('events', JSON.stringify(x))
	}
}


const App = () => {
	local()

  return (
    <div className="App">
    	<Router>
    		<Switch>
    			<Route path="/agenda-app/stats" component={Stats}/>
    			<Route path="/agenda-app/settings" component={Settings}/>
    			<Route path="/agenda-app/" component={Agenda}/>
    		</Switch>
    	</Router>
    </div>
  );
}

export default App;
